<template>
  <div id="app">
    <keep-alive include="detail">
      <router-view/>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Arial, "微软雅黑";
}
img {
  border: none;
  max-width: 100%;
  vertical-align: middle;
}
body,
p,
form,
input,
button,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-x: hidden;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}
input,
textarea {
  -webkit-user-select: text;
  -ms-user-select: text;
  user-select: text;
  -webkit-appearance: none;
  font-size: 1em;
  line-height: 1.5em;
}
table {
  border-collapse: collapse;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
a {
  outline: 0;
  cursor: pointer;
}
a:link,
a:active {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  color: #1eb6a9;
  text-decoration: none;
}
a {
  text-decoration: none;
  -webkit-touch-callout: none;
}
em,
i {
  font-style: normal;
}
li,
ol {
  list-style: none;
}
html {
  font-size: 16px;
}
.clear {
  clear: both;
  height: 0;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
  overflow: hidden;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
body {
  margin: 0 auto;
  max-width: 640px;
  min-width: 320px;
  color: #555;
  background: #f1f1f1;
  height: 100%;
}
a {
  color: #222;
  text-decoration: none;
}
.router-link-active {
  color: #1eb6a9 !important;
}
</style>



